(function() {
	'use strict';

	//定义应用
	window.ngApp = angular.module('ngseed', [
		'ngCookies',
		'ngTouch',
		'ngResource',
		'ui.router',
		'toastr'
	]);

	//配置
	ngApp.config(function($logProvider, toastrConfig) {
		'ngInject';

		// Enable log
		$logProvider.debugEnabled(true);

		// Set options third-party lib
		toastrConfig.allowHtml = true;
		toastrConfig.timeOut = 3000;
		toastrConfig.positionClass = 'toast-top-right';
		toastrConfig.preventDuplicates = true;
		toastrConfig.progressBar = true;
	});

	//启动
	ngApp.run(function($log) {
		'ngInject';

		$log.debug('已经启动！');
	});

})();
(function() {
    'use strict';

    ngApp.config(function($stateProvider, $urlRouterProvider) {
            'ngInject';
            
            $stateProvider
                .state('home', {
                    url: '/',
                    templateUrl: 'app/tpls/pages/home.tpl.html',
                    controller: 'homeCtrl'
                });

            $urlRouterProvider.otherwise('/');

        });


})();
(function() {
    'use strict';

    ngApp.controller('headerCtrl', function($scope, $location) {
        'ngInject';

        $scope.isActive = function(path) {
            return $location.path().match(path);
        }

    });

})();

(function() {
    'use strict';

    ngApp.controller('homeCtrl', function($scope) {
        'ngInject';

    });

})();

